@mixin btn-gradient-color {
  background-image: linear-gradient(to right, #50ffc5, #19b9c3);
  background-image: linear-gradient(120deg, #50FFC5 , #19B9C3 60%) padding-box;
}

@mixin flexCenter {
  display: flex;
  align-items: center;
  justify-content: center;
}
